import Vue from 'vue'
import Router from 'vue-router'
import Home from '../Home'
import Tzgg from '../Tzgg'
import Zlsq from '../Zlsq'
import Zlfy from '../Zlfy'
import Zlzz from '../Zlzz'
import Zldl from '../Zldl'
import Zlzx from '../Zlzx'
import Qtyw from '../Qtyw'
import About from '../About'

import Electronics from '../pages/Zlsq/Electronics'
import paper from '../pages/Zlsq/paper'
import pay from '../pages/Zlsq/pay'
import Costintro from '../pages/Zlfy/Costintro'
import accountInfo from '../pages/Zlfy/accountInfo'
import policies from '../pages/Zldl/policies'
import Quickentry from '../pages/Zldl/Quickentry'
//---------------------------------------------分割线---------------------------------------------------
import Agencybook from '../pages/About/Agencybook'
import Stafftable from '../pages/About/Stafftable'

import fyjh from '../pages/Qtyw/fyjh'
import Qtywzydj from '../pages/Qtyw/zydj'
import Qtywdjbfb from '../pages/Qtyw/djbfb'
import Qtywhtba from '../pages/Qtyw/htba'
import Qtywyxsc from '../pages/Qtyw/yxsc'
import Qtywplflztyw from '../pages/Qtyw/plflztyw'

import ProxyGuide from '../pages/Zldl/ProxyGuide'
import ProxyGuideremark from '../pages/Zldl/ProxyGuide/remark'
import ProxyGuidepart1 from '../pages/Zldl/ProxyGuide/part1'
import ProxyGuidepart2 from '../pages/Zldl/ProxyGuide/part2'
import ProxyGuidepart3 from '../pages/Zldl/ProxyGuide/part3'
import ProxyGuidepart4 from '../pages/Zldl/ProxyGuide/part4'
import ProxyGuidepart5 from '../pages/Zldl/ProxyGuide/part5'

import Djzdfengcai from '../pages/Djzd/fengcai'
import Djzdyuandi from '../pages/Djzd/yuandi'
import Djzdzhidu from '../pages/Djzd/zhidu'
import Djzddongtai from '../pages/Djzd/dongtai'

import Qtgzfunv from '../pages/Qtgz/funv'
import Qtgzqingnian from '../pages/Qtgz/qingnian'
import Qtgzzhijia from '../pages/Qtgz/zhijia'

import whchengguo from '../pages/Whjs/whchengguo'
import whzhanshi from '../pages/Whjs/whzhanshi'
//---------------------------------------------分割线---------------------------------------------------
import TzggDetail from '../TzggDetail'
import DjzdDetail from '../DjzdDetail'
import QtgzDetail from '../QtgzDetail'
import WhjsDetail from '../WhjsDetail'
import HonorWallDetail from '../HonorWallDetail'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'Home',
			redirect: '/Home'
		},
		{
			path: '/Home',
			name: 'Home',
			component: Home
		},
		{
			path: '/Tzgg',
			name: 'Tzgg',
			component: Tzgg
		},
		{
			path: '/Zlsq',
			name: 'Zlsq',
			component: Zlsq,
			children: [{ path: '/', redirect: '/Zlsq/paper' },
				{ path: '/Zlsq/paper', component: paper },
				{ path: '/Zlsq/Electronics', component: Electronics },
				{ path: '/Zlsq/pay', component: pay }
			]
		},
		{
			path: '/Zlfy',
			name: 'Zlfy',
			component: Zlfy,
			children: [{ path: '/', redirect: '/Zlfy/Costintro' },
				{ path: '/Zlfy/Costintro', component: Costintro },
				{ path: '/Zlfy/accountInfo', component: accountInfo }
			]
		},
		{
			path: '/Zlzz',
			name: 'Zlzz',
			component: Zlzz
		},
		{
			path: '/Zldl',
			name: 'Zldl',
			component: Zldl,
			children: [{ path: '/', redirect: '/Zldl/ProxyGuide' },
				{ path: '/Zldl/policies', component: policies },
				{ path: '/Zldl/Quickentry', component: Quickentry },
				{
					path: '/Zldl/ProxyGuide',
					component: ProxyGuide,
					children: [{ path: '/', redirect: '/Zldl/ProxyGuide/remark' },
						{ path: '/Zldl/ProxyGuide/remark', component: ProxyGuideremark },
						{ path: '/Zldl/ProxyGuide/part1', component: ProxyGuidepart1 },
						{ path: '/Zldl/ProxyGuide/part2', component: ProxyGuidepart2 },
						{ path: '/Zldl/ProxyGuide/part3', component: ProxyGuidepart3 },
						{ path: '/Zldl/ProxyGuide/part4', component: ProxyGuidepart4 },
						{ path: '/Zldl/ProxyGuide/part5', component: ProxyGuidepart5 }
					]
				}
			]
		},
		{
			path: '/Zlzx',
			name: 'Zlzx',
			component: Zlzx
		},
		{
			path: '/Qtyw',
			component: Qtyw

		},
		{
			path: '/About',
			name: 'About',
			component: About
		},
		{
			path: '/About/Agencybook',
			component: Agencybook
		},
		{
			path: '/About/Stafftable',
			component: Stafftable
		},
		{
			path: '/Tzgg/TzggDetail',
			component: TzggDetail
		},
		{
			path: '/Qtyw/fyjh',
			component: fyjh
		}, 
		{
			path: '/Qtyw/zydj',
			component: Qtywzydj
		},
		{
			path: '/Qtyw/djbfb',
			component: Qtywdjbfb
		},{
			path: '/Qtyw/htba',
			component: Qtywhtba
		},{
			path: '/Qtyw/yxsc',
			component: Qtywyxsc
		},{
			path: '/Qtyw/plflztyw',
			component: Qtywplflztyw
		},
		{
			path: '/Home/DjzdDetail',
			component: DjzdDetail,
			children: [{ path: '/', redirect: '/Home/DjzdDetail/fengcai' },
				{ path: '/Home/DjzdDetail/fengcai', component: Djzdfengcai },
				{ path: '/Home/DjzdDetail/yuandi', component: Djzdyuandi },
				{ path: '/Home/DjzdDetail/zhidu', component: Djzdzhidu },
				{ path: '/Home/DjzdDetail/dongtai', component: Djzddongtai }
			]
		},
		{
			path: '/Home/QtgzDetail',
			component: QtgzDetail,
			children: [{ path: '/', redirect: '/Home/QtgzDetail/funv' },
				{ path: '/Home/QtgzDetail/funv', component: Qtgzfunv },
				{ path: '/Home/QtgzDetail/qingnian', component: Qtgzqingnian },
				{ path: '/Home/QtgzDetail/zhijia', component: Qtgzzhijia }
			]
		},
		{
			path: '/Home/HonorWallDetail',
			component: HonorWallDetail
		},
		{
			path: '/Home/WhjsDetail',
			component: WhjsDetail,
			children: [{ path: '/', redirect: '/Home/WhjsDetail/whchengguo' },
				{ path: '/Home/WhjsDetail/whchengguo', component: whchengguo },
				{ path: '/Home/WhjsDetail/whzhanshi', component: whzhanshi }
			]
		}
	]
})


