<template>
	<div>
		<div to="/Zlfy/Costintro" class="Block">
			{{title}}
		</div>
		<div class="libeGray"></div>
		<div class="row">
			<div v-for="item in items" class="col-lg-6">
				<a href="#">
					{{item}}
				</a>
			</div>
		</div>
	</div>
</template>

<script>
	var datas = [
		["<<专利缴费指南>>", "<<专利费用网上缴费操作系统指南>>", "<<国家知识产权局专利收费标准一览表>>", "<<国家知识产权局PCT申请的收费标准>>"],
		["<<重庆市专利资助明细表>>", "<<重庆市专利资助申请表>>"],
		["<<重庆市专利代理明细表>>", "<<重庆市专利代理申请表>>"]
	]
	export default {
		props: ['frome', 'title'],
		name: 'download',
		data() {
			return {
				items: {}
			}
		},
		mounted() {
			if(this.frome == "Zlfy") {
				this.items = datas[0];
			} else if(this.frome == "Zlzz") {
				this.items = datas[1];
			} else {
				this.items = datas[2];
			}
		}
	};
</script>

<style scoped>
	.Block {
		line-height: 3em;
		color: #666666;
		font-size: larger;
		text-align: center;
		display: inline-block;
	}
	
	.libeGray {
		height: 1px;
		background-color: #E6E6E6;
	}
	
	.row {
		line-height: 2em;
		padding: 20px;
		font-size: medium;
	}
</style>


