  return _c('div', [_c('pre', [_vm._v("\n\t国家知识产权局作为国务院专利行政管理部门，依法承担专利代理人资格认定、专利代理机构设立审批等行政许可事项的实施。本指南依据《专利法》《专利代理条例》和相关部门规章就专利代理相关行政审批事项编制服务指南，包含了设定依据、申请条件、申请材料、基本流程、审批时限、收费依据及标准、注意事项等内容的说明。相关内容发生变更时将及时进行修订。\n专利代理人资格认定、专利代理机构设立审批两项行政许可的具体信息如下：\n项目\n编码\t项目名称\t子项\t审批\n类别\t设定依据\n33001\t专利代理人资格认定\t无\t行政\n许可\t《专利代理条例》（国务院令第76号）“第二条本条例所称专利代理是指专利代理机构以委托人的名义，在代理权限范围内，办理专利申请或者办理其他专利事务。第十四条本条例所称专利代理人是指获得《专利代理人资格证》，持有《专利代理人工作证》的人员。第十五条拥护中华人民共和国宪法，并具备下列条件的中国公民，可以申请专利代理人资格：（一）十八周岁以上，具有完全的民事行为能力；（二）高等院校理工科专业毕业（或者具有同等学历），并掌握一门外语；（三）熟悉专利法和有关的法律知识；（四）从事过两年以上的科学技术工作或者法律工作。第十六条申请专利代理人资格的人员，经本人申请，专利代理人考核委员会考核合格的，由中国专利局发给《专利代理人资格证书》。专利代理人考核委员会由中国专利局、国务院有关部门以及专利代理人的组织的有关人员组成。”\n33002\t专利代理机构设立审批\t1.设立专利代理机构审批\t行政\n许可\t《专利代理条例》（国务院令第76号）“第二条本条例所称专利代理是指专利代理机构以委托人的名义，在代理权限范围内，办理专利申请或者办理其他专利事务。第三条本条例所称专利代理机构是指接受委托人的委托，在委托权限范围内，办理专利申请或者办理其他专利事务的服务机构。专利代理机构包括：（一）办理涉外专利事务的专利代理机构；（二）办理国内专利事务的专利代理机构；（三）办理国内专利事务的律师事务所。第五条向专利管理机关申请成立专利代理机构，应当提交下列文件：（一）成立专利代理机构的申请书，并写明专利代理机构的名称、办公场所、负责人姓名；（二）专利代理机构章程；（三）专利代理人姓名及其资格证书；（四）专利代理机构资金和设施情况的书面证明。第六条申请成立办理国内专利事务的专利代理机构，或者律师事务所申请开办专利代理业务的，应当经过其主管机关同意后，报请省、自治区、直辖市专利管理机关审查；没有主管机关的，可以直接报请省、自治区、直辖市专利管理机关审查。审查同意的，由审查机关报中国专利局审批。申请成立办理涉外专利事务的专利代理机构，应当依照《中华人民共和国专利法》的有关规定办理。办理涉外专利事务的专利代理机构，经中国专利局批准的，可以办理国内专利事务。第七条专利代理机构自批准之日起成立，依法开展专利代理业务，享有民事权利，承担民事责任。”\n\t\t2.专利代理机构注册事项变更审批\t行政\n许可\t《专利代理条例》（国务院令第76号）“第十二条专利代理机构变更机构名称、地址和负责人的，应当报中国专利局予以变更登记，经批准登记后，变更方可生效。专利代理机构注销，应当在妥善处理各种尚未办结的事项后，向原审查机关申报，并由该机关报中国专利局办理有关手续。”\n\t\t3.专利代理机构注销审批\t行政\n许可\t《专利代理条例》（国务院令第76号）“第十三条已批准的专利代理机构，因情况变化不再符合本条例第四条规定的条件，并在一年内仍不能具备这些条件的，原审查的专利管理机关应当建议中国专利局撤销该专利代理机构。”\n《专利代理管理办法》（国家知识产权局令第30号）“第十一条专利代理机构注销或者撤销的……向其所在地的省、自治区、直辖市的知识产权局申请……并向国家知识产权局办理注销或撤销手续。”\n\t\t4.律师事务所申请开办专利代理业务审批\t行政\n许可\t《专利代理条例》（国务院令第76号）“第四条专利代理机构的成立，必须符合下列条件：（一）有自己的名称、章程、固定办公场所；（二）有必要的资金和工作设施；（三）财务独立，能够独立承担民事责任；（四）有三名以上具有专利代理人资格的专职人员和符合中国专利局规定的比例的具有专利代理人资格的兼职人员。律师事务所开办专利代理业务的，必须有前款第四项规定的专职人员。”\n《专利代理管理办法》（国家知识产权局令第30号）“第九条律师事务所申请开办专利代理业务的，参照上述规定进行审批。”\n注：本表引自国家知识产权局公布的行政审批事项汇总清单\n\n\t\t")])])
