<template>
	<pre>
1 . 什么是电子申请

电子申请是指以互联网为传输媒介将专利申请文件以符合规定的电子文件形式向国家知识产权局提交发明丶实用新型和外观设计专利申请和中间文件，
以及中国国家阶段的国际申请和中间文件。

2 . 申请人如何使用电子申请

(1). 首先办理用户注册手续，获得用户代码和密码。
(2). 登陆电子申请网站，下载并安装数字证书和客户端软件
(3). 进行客户端和升级程序的网络配置
(4). 利用客户端或离线制作和编辑电子申请文件
(5). 数字证书签名电子申请文件
(6). 提交电子申请文件
(7). 接受电子回执
(8). 提交电子申请后，可随时登陆电子申请网站查询电子申请相关信息
(9). 通过电子申请系统接收通知书，针对所提交的电子申请提交中间文件
