办理流程
</pre>
<img src="../../assets/flow/zydj.png"/>
<pre>
办结时限：7个工作日,最快3个工作日。提交文件存在缺陷的，自缺陷完全消除之日起算。
（二）需提交的文件（提交文件一式一份）

1.专利权质押登记申请表
2.专利权质押合同 
3.双方当事人的身份证明
4.双方当事人委托书 
5.被委托人的身份证明复印件
6.专利权质押登记申请表中注明有专利权评估报告的，应当提交专利权评估报告
7. 其他需要提供的材料
（三）不予质押登记的情形

1.出质人与专利权人不一致的；
2.专利权已终止或已被宣告无效的；
3.专利申请尚未被授予专利权的；
4.专利权处于年费缴纳滞纳期的；
5.专利权已被启动无效宣告程序的；
6.因专利权的归属发生纠纷或者人民法院裁定对专利权采取保全措施，专利权的质押手续被暂停办理的；
7.债务人履行债务的期限超过专利权期的；
8.质押合同约定在债务履行届满未受清偿时，专利权归质权人所有的；
9.质押合同不符合《专利权质押登记办法》第九条规定的；
10.以共有专利权出质但未取得全体共有人同意的；
11.专利权已被申请质押登记且处于质押期间；
12.其他应当不予登记的情形。
（四）专利权质押变更及注销申请需提交的文件(提交文件一式一份)

1.专利权质押变更或注销申请表
2.变更协议或质权消灭的证明材料
3.双方当事人委托书
4.被委托人的身份证明
5.专利权质押登记通知书
6.其他需要提供的材料
