											第三部分  专利代理机构注册事项变更

一、适用范围
本部分适用于专利代理机构注册事项变更审批事项的申请和办理。
二、项目信息
项目名称：专利代理机构设立审批
子项名称：专利代理机构注册事项变更审批
审批类别：行政许可
项目编码：33002
三、办理依据
《专利代理条例》（国务院令第76号）第十二条：
第十二条 专利代理机构变更机构名称、地址和负责人的，应当报中国专利局予以变更登记，经批准登记后，变更方可生效。专利代理机构注销，应当在妥善处理各种尚未办结的事项后，向原审查机关申报，并由该机关报中国专利局办理有关手续。
四、受理机构
国家知识产权局
五、决定机构
国家知识产权局
六、审批数量
无限制
七、办事条件
专利代理机构和开办专利代理业务的律师事务所的名称、地址、邮编、负责人、合伙人或者股东、同时具有律师执业资格和专利代理人资格的人员、注册资金等注册事项发生变更的，应当及时向国家知识产权局申请办理变更手续，变更经国家知识产权局批准后生效。专利代理机构在国家知识产权局登记的信息应当与其在工商行政管理部门登记的信息一致。
对变更后的负责人、合伙人或股东的审批标准、对证明材料的要求与审批专利代理机构设立申请的审批标准相同。
八、申请接收方式
所有申请均通过专利代理管理系统（http://dlgl.sipo.gov.cn,以下简称管理系统）提出。
