图3 专利代理机构注册事项变更流程
九、申请材料和办理基本流程
申请变更专利代理机构注册事项的，应当以专利代理机构身份在管理系统申请。总体流程如图3所示。
国家知识产权局对申请材料电子件进行审核。申请材料不齐全或者不符合法定形式的，在收到电子申请的5个工作日内一次性通过管理系统书面告知申请人需要补正的全部内容。符合规定的, 形成电子受理单。审批时限从形成电子受理单之日(即为受理日)起算。

注：变更事项涉及退休党政领导干部的，应当上传相应组织（人事）部门出具的同意其在专利代理机构执业证明的电子件。
1.专利代理机构名称变更
变更事项	步骤	用户	申请材料和办理步骤
专利代理机构名称变更	1	代理机构用户	进行名称预核准。
	2		登录专利代理管理系统，在线填写《注册事项变更申请表》。
	3		上传下列文件的电子件：
（1）专利代理机构合伙协议书或章程修正案；
（2）经签字或者盖章的专利代理机构设立审批事项告知承诺书；
	4	国家局	在线审查《注册事项变更申请表》及电子扫描件。
	5		审核申请材料，完成变更。
	6	代理机构用户	交回注册证正、副本
