十、办理方式
    一般程序，即包括申请、受理、审查与决定、证件（文书）制作与送达、结果公开等。
十一、审批时限
审批时限从受理日起算，国家知识产权局应当自受理日起20个工作日内作出审批决定。
十二、审批收费依据与标准
本行政许可事项不收费。
十三、审批结果
作出行政决定后，管理系统实时显示审批决定，国家知识产权局发出《关于批准XXXX（机构名称）变更注册事项的通知》或者不予变更的审批决定。
十四、结果送达
作出行政决定后，管理系统实时显示审批决定，国家知识产权局通过邮寄或者当面领取的方式发放《关于批准XXXX（机构名称）变更注册事项的通知》和变更后的《专利代理机构注册证》（正本和/或副本）。
十五、申请人权利和义务
符合规定的申请人有权变更专利代理机构的注册事项。
申请人应当保证申请材料的真实合法，不弄虚作假。在审批过程中积极配合，及时进行补正，不以任何方式干扰审批工作。
对相关审批决定不服的，可以根据行政复议法提起行政复议，也可以根据行政诉讼法提出行政诉讼。
