},staticRenderFns: [function (){var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;
  return _c('div', [_c('pre', [_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t第四部分  专利代理机构的注销\n\n一、适用范围\n本部分适用于专利代理机构注销审批事项的申请和办理。\n二、项目信息\n项目名称：专利代理机构设立审批\n子项名称：专利代理机构注销审批\n审批类别：行政许可\n项目编码：33002\n三、办理依据\n《专利代理条例》（国务院令第76号）第十三条：\n已批准的专利代理机构，因情况变化不再符合本条例第四条规定的条件，并在一年内仍不能具备这些条件的，原审查的专利管理机关应当建议中国专利局撤销该专利代理机构。”\n《专利代理管理办法》（国家知识产权局令第70号）第十二条：\n专利代理机构停业或者撤销的……向其所在地的省、自治区、直辖市的知识产权局申请……并向国家知识产权局办理停业或撤销手续。\n四、受理机构\n国家知识产权局\n五、决定机构\n国家知识产权局\n六、审批数量\n无限制\n七、办事条件\n专利代理机构注销的，应当在注销前与所有委托人转委托或者解除委托，妥善处理各种尚未办结的专利代理业务（包括其分支机构的所有业务）。\n八、申请材料\n专利代理机构及其分支机构的专利代理事务处理情况说明的电子件。其中应当包含对全部尚未办结的业务（包括其分支机构的所有业务）的处理情况说明，以及对所有代理的专利申请转委托或解除委托的说明。说明应由全体合伙人/股东签字。\n九、申请接收方式\n所有申请均通过专利代理管理系统（http://dlgl.sipo.gov.cn,以下简称管理系统）提出。\n十、办理基本流程\n申请注销专利代理机构注册的，应当以专利代理机构身份在管理系统申请，总体流程如图4所示。\n"), _c('img', {
    attrs: {
      "src": require("../../../assets/flow/part4.png")
    }
  }), _vm._v("\n图4 注销专利代理机构流程\n\n（一）提交注销申请\n专利代理机构申请注销的，应当通过管理系统向国家知识产权局提出注销申请，同时上传第八点要求材料的电子件。\n（二）机构注销申请的受理\n国家知识产权局对申请材料电子件进行审核，检查有无对全部尚未办结的业务（包括其分支机构的所有业务）的处理情况说明，检查该机构是否有尚未转委托或者解除委托的专利代理业务。对不符合要求的，在收到电子申请的5个工作日内一次性通过管理系统书面告知申请人需要补正的全部内容。符合规定的,形成电子受理单。审批时限从形成电子受理单之日（即受理日）起算。\n（三）机构注销申请的审批\n国家知识产权局在收到申请后，对申请材料进行审核。对于符合规定条件的申请，应当自受理日起20个工作日内作出批准注销的审批决定，向申请人发出注销通知。对于不符合规定条件的申请，应当自受理日起20个工作日内作出不予批准的审批决定，并向申请人发出不予批准通知。\n十一、办理方式\n    一般程序，即包括申请、受理、审查与决定、证件（文书）制作与送达、结果公开等。\n十二、审批时限\n审批时限从受理日起算，国家知识产权局应当自受理日起20个工作日内作出审批决定。\n十三、审批收费依据与标准\n本行政许可事项不收费。\n十四、审批结果\n予以批准的，国家知识产权局发出《关于注销XXXX（机构名称）的通知》；不予批准的，发出不予批准的审批决定。\n十五、结果送达\n作出行政决定后，管理系统实时显示审批决定，国家知识产权局通过邮寄或者当面领取的方式发放《关于注销XXXX（机构名称）的通知》或者不予批准的审批决定。\n十六、申请人权利和义务\n符合规定的申请人有权注销专利代理机构。\n申请人应当保证申请材料的真实合法，不弄虚作假。在审批过程中积极配合，及时进行补正，不以任何方式干扰审批工作。\n对相关审批决定不服的，可以根据行政复议法提起行政复议，也可以根据行政诉讼法提出行政诉讼。\n十七、咨询途径\n（一）窗口咨询：国家知识产权局\n（二）电话咨询：国家知识产权局（010）62087927\n（三）电子邮件咨询：国家知识产权局dailiguanli@sipo.gov.cn\n（四）信函咨询：\n咨询部门名称：国家知识产权局条法司专利代理管理处\n通讯地址：北京市海淀区西土城路6号国家知识产权局条法司专利代理管理处\n邮政编码：100088\n联系电话：（010）62086618\n十八、投诉渠道\n（一）电话投诉：（010）62083357\n（二）网上投诉：http://app.sipo.gov.cn:8080/jzxx/sendxf.html\n（三）信函投诉：\n投诉部门：国家知识产权局信访室 \n通讯地址：北京市海淀区西土城路6号国家知识产权局 \n邮政编码：100088\n十九、办公地址和时间\n（一）办公地址：\n国家知识产权局：北京市海淀区西土城路6号国家知识产权局条法司专利代理管理处\n（二）办公时间：\n国家知识产权局：工作日8:30至11:30；13:30至16:30\n（三）乘车路线：\n国家知识产权局：北京市公交“蓟门桥”公交站；地铁10号线“西土城路”站南800米。\n二十、公开查询\n申请人可以随时通过专利代理管理系统（http://dlgl.sipo.gov.cn/）查询审批状态和结果，公众可以通过国家知识产权局网站（http://www.sipo.gov.cn/）查询专利代理机构著录事项的实时信息。\n\n\t\t")])])
